<template>
    <div class="BxCountdown">
        <span class="BxCountdown__dot"></span>
        <div class="BxCountdown__textLive">LIVE</div>

        <div class="BxCountdown__Countdown" v-if="statusType === 'running' && statusType !== 'expired'">
            <div class="hour">
                <span class="number">{{ hours }}</span>
            </div>
            <div class="split">
				:
			</div>
            <div class="min">
                <span class="number">{{ minutes }}</span>
            </div>
			<div class="split">
				:
			</div>
            <div class="sec">
                <span class="number">{{ seconds }}</span>
            </div>
        </div>
		
		<div v-if="statusType == 'expired'" style="margin-left:10px">
			Expired
		</div>
		
    </div>
</template>

<script>
		
	export default {
		props:[
			'startTime', 'endTime', 'trans'
		],
		data () {
			return {
				timer: "",
				wordString: {},
				start: "",
				end: "",
				interval: "",
				days: "",
				minutes: "",
				hours: "",
				seconds: "",
				message: "",
				statusType: "",
				statusText: "",
			}
		},
		created () {
			this.wordString = JSON.parse(this.trans)
		},
		mounted () {
			this.start = new Date(this.startTime).getTime()
			this.end = new Date(this.endTime).getTime()
			//Update the Count every 1 Second
			this.timerCount(this.start, this.end)
			this.interval = setInterval(() => {
				this.timerCount(this.start, this.end)
			}, 1000)
		
		},
		methods:{
            timerCount(start, end){
                //Get todays date and time
                var now = new Date().getTime()
                //Find the distance between now and the count down date
                var distance = start - now
                var passTime = end - now
                if(distance < 0 && passTime < 0){
                    this.message = this.wordString.expired
                    this.statusType = "expired"
                    this.statusText = this.wordString.status.expired
                    clearInterval(this.interval)
                    return
                }else if (distance < 0 && passTime > 0){
                    this.calcTime(passTime)
                    this.message = this.wordString.running
                    this.statusType = "running"
                    this.statusText = this.wordString.status.running
                }else if(distance > 0 && passTime > 0){
                    this.calcTime(distance)
                    this.message = this.wordString.upcoming
                    this.statusType = "upcoming"
                    this.statusType = this.wordString.status.upcoming
                }
			},
            calcTime(dist){
                // Time calculations for days, hours, minutes and seconds
                this.days = Math.floor(dist / (1000 * 60 * 60 * 24)).toLocaleString(undefined, {minimumIntegerDigits: 2});

                this.hours = Math.floor((dist % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)).toLocaleString(undefined, {minimumIntegerDigits: 2});

                this.minutes = Math.floor((dist % (1000 * 60 * 60)) / (1000 * 60)).toLocaleString(undefined, {minimumIntegerDigits: 2});

                this.seconds = Math.floor((dist % (1000 * 60)) / 1000).toLocaleString(undefined, {minimumIntegerDigits: 2})
            }
		},
		computed: {
			InFiveMinutes(){
				if(this.minutes <= '05' && this.days == '00' && this.hours == '00'){
					return 'Five'
				}else{
					return ''
				}
			},
			inOneMinutes(){
				if(this.seconds <= '59' && this.days == '00' && this.hours == '00' && this.minutes == '00'){
					return 'One'
				}else{
					return ''
				}
			}
		},
		watch: {
			statusType(val){
				if (val == 'expired'){
					console.log(val)
				}
			},
			InFiveMinutes(val){
				if(val == 'Five'){
					console.log('Session will be end in five minutes')
				}else{
					return ''
				}
				
			},
			inOneMinutes(val){
				if(val == 'One'){
					console.log('One Minutes Left, Your Session Automatically will be directed to Session Summary')
				}else{
					return ''
				}
				
			}
		},
	}
</script>
